import React, { Component } from 'react';
import Card from 'react-bootstrap/Card'
import { Button, Table } from 'react-bootstrap';
import Form from 'react-bootstrap/Form';
import Header from "../Header";
import ClipLoader from "react-spinners/ClipLoader";
import axios from "axios";
import { withRouter } from 'react-router-dom';
import S3 from 'react-aws-s3';
import moment from 'moment';
const dotenv = require('dotenv');
class AddDriver extends Component {

    state = {
        userId: '', driverId: 0, item: [], error: '', isLoading: false, loadingColor: '#ffffff', pageNo: 0, firstName: '', lastName: '', mobileNo: '', email: '', licenseNo: '',
        licenseUrl: '', licenseFile: '', licenseFileName: '', imageData: ''
    };
    constructor(props) {
        super(props);
    }
    componentDidMount() {
        this.setState({ isLoading: true });
        let userId = localStorage.getItem("userId");
        console.log("userId:"+userId);
        this.setState({ userId: userId });


        this.getDrivers(userId);
    }

    showPopup(object) {

        this.prePayment(object);
    }
    async getDrivers(userId) {

        //const headers = { 'Content-Type': 'application/json' } 
        let token = localStorage.getItem("token");
        let pageNo = this.state.pageNo + 1;
        const headers = { 'Authorization': `Bearer ${token}` };
        let urlData = "&userId=" + userId + "&pageId=" + pageNo;
        //const response = await fetch('http://localhost:3001/booking/getCabs?originObj='+originObj+'&destinationObj='+destinationObj, { headers });
        try {
            const response = await fetch(process.env.REACT_APP_APIURL + 'agent/get_drivers?' + urlData, { headers });

            const dataRes = await response.json();


            if (dataRes.code == 200) {

                this.setState({ item: dataRes.data });
            } else {
                console.log("errorr")
                //this.setState({error:'some internal error please try later'})
            }

        } catch (error) {
            console.log(error)
        }

        this.setState({ isLoading: false });
        //this.setState({cabsList:data.data});
    }

    addDriver = async () => {
        try {
            let token = localStorage.getItem("token");

            const headers = { 'Authorization': `Bearer ${token}` };
            let carModelNo = '';
            let carNo = '';
            if (this.state.firstName == "" || this.state.firstName == null) {
                this.setState({ error: "Please enter first name" })
                return false;
            }
            if (this.state.lastName == "" || this.state.lastName == null) {
                this.setState({ error: "Please enter last name" })
                return false;
            }
            if (this.state.mobileNo == "" || this.state.mobileNo == null) {
                this.setState({ error: "Please enter mobile number" })
                return false;
            }
            if (this.state.licenseNo == "" || this.state.licenseNo == null) {
                this.setState({ error: "Please enter license number" })
                return false;
            }
            if (this.state.licenseUrl == "" || this.state.licenseUrl == null) {
                this.setState({ error: "Please enter license image" })
                return false;
            }
            let urlData = "&userId=" + this.state.userId + "&firstName=" + this.state.firstName + "&lastName=" + this.state.lastName + "&mobileNo=" + this.state.mobileNo + "&email=" + this.state.email + "&licenseNo=" + this.state.licenseNo + "&licenseUrl=" + this.state.licenseUrl;
            //const response = await fetch('http://localhost:3001/booking/getCabs?originObj='+originObj+'&destinationObj='+destinationObj, { headers });

            //const response = await fetch('http://localhost:3001/agent/add_driver?'+urlData, { headers });
            const data = {
                userId: this.state.userId,
                firstName: this.state.firstName,
                lastName: this.state.lastName,
                mobileNo: this.state.mobileNo,
                email: this.state.email,
                licenseNo: this.state.licenseNo,
                licenseUrl: this.state.licenseUrl,
            };


            const result = await axios.post(process.env.REACT_APP_APIURL + "agent/add_driver", data, { headers });
            console.log("result:" + JSON.stringify(result));
            if (result.data.code == 200) {
                this.setState({ error: result.data.message });
                this.getDrivers(this.state.userId);
            } else {

                this.setState({ error: result.data.message })
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }
        this.setState({ isLoading: false });
    }
    deleteDriver = async (driverId) => {
        try {

            if (driverId < 1 || driverId == null) {
                this.setState({ error: "Please Select driver to edit" })
                return false;
            }

            this.setState({ isLoading: true });
            let token = localStorage.getItem("token");
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&driverId=" + driverId;

            let cabData = await axios.delete(process.env.REACT_APP_APIURL + 'agent/driver?' + urlData, { headers });

            if (cabData.data.code == 200) {
                this.setState({ error: 'Driver deleted' });
                this.getDrivers(this.state.userId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    editDriver = async () => {
        try {

            if (this.state.driverId == "" || this.state.driverId == null) {
                this.setState({ error: "Please Select driver to edit" })
                return false;
            }
            if (this.state.firstName == "" || this.state.firstName == null) {
                this.setState({ error: "Please enter first name" })
                return false;
            }
            if (this.state.lastName == "" || this.state.lastName == null) {
                this.setState({ error: "Please enter last name" })
                return false;
            }
            if (this.state.mobileNo == "" || this.state.mobileNo == null) {
                this.setState({ error: "Please enter mobile number" })
                return false;
            }
            if (this.state.licenseNo == "" || this.state.licenseNo == null) {
                this.setState({ error: "Please enter license number" })
                return false;
            }
            if (this.state.licenseUrl == "" || this.state.licenseUrl == null) {
                this.setState({ error: "Please enter license image" })
                return false;
            }
            this.setState({ isLoading: true });
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            const urlData = {
                userId: this.state.userId,
                firstName: this.state.firstName,
                lastName: this.state.lastName,
                mobileNo: this.state.mobileNo,
                email: this.state.email,
                licenseNo: this.state.licenseNo,
                licenseUrl: this.state.licenseUrl,
                driverId: this.state.driverId
            };
            let cabData = await axios.put(process.env.REACT_APP_APIURL + 'agent/update_driver?', urlData, { headers });

            if (cabData.data.code == 200) {
                this.setState({ error: 'Driver updated' });
                this.getDrivers(this.state.userId);
            } else {
                this.setState({ error: 'Something went wrong' });
            }
        } catch (err) {

            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });
    }
    viewDriver = async (driverId) => {

        try {
            this.setState({ cabId: 0 })
            let token = localStorage.getItem("token");
            let pageNo = this.state.pageNo + 1;
            const headers = { 'Authorization': `Bearer ${token}` };
            let urlData = "&userId=" + this.state.userId + "&driverId=" + driverId;
            let cabData = await axios.get(process.env.REACT_APP_APIURL + 'agent/get_driverbyid?' + urlData, { headers });

            if (cabData.data.code == 200) {
                this.setState({ firstName: cabData.data.data.firstName });
                this.setState({ lastName: cabData.data.data.lastName });
                this.setState({ mobileNo: cabData.data.data.mobileNo });
                this.setState({ email: cabData.data.data.email });
                this.setState({ licenseNo: cabData.data.data.idNumber });
                this.setState({ licenseUrl: cabData.data.data.idProof });
                this.setState({ driverId: driverId });

            } else {

            }
        } catch (err) {
            console.log("error:" + JSON.stringify(err.response));
            this.setState({ isOtpSent: 'N' });
            this.setState({ error: err.response.data.message });
        }

        this.setState({ isLoading: false });

    }
    setFirstName = (firstName) => {
        this.setState({ firstName: firstName.target.value });
    }

    setLastName = (lastName) => {
        this.setState({ lastName: lastName.target.value });
    }
    setMobileNo = (mobileNo) => {
        this.setState({ mobileNo: mobileNo.target.value });
    }
    setEmail = (email) => {
        this.setState({ email: email.target.value });
    }
    setLicenseNo = (licenseNo) => {
        this.setState({ licenseNo: licenseNo.target.value });
    }
    setLicenseUrl = async (event) => {
        try {
            this.setState({ error: '' });
            let fileData = event.target.files[0];
            let fileSize = fileData.size;
            let fileName = fileData.name;
            let ext = fileName.split('.').pop();
            let fileName_new = moment.now() + "." + ext;


            if (fileSize > 110000) {
                this.setState({ error: 'Image size must be <100kb' });
                this.setState({ licenseUrl: '' });
                return false;
            }

            this.setState({ isLoading: true });

            const config = {
                bucketName: process.env.REACT_APP_BUCKET_NAME,
                dirName: 'drivers', /* optional */
                region: process.env.REACT_APP_REGION,
                accessKeyId: process.env.REACT_APP_S3ACCESS_KEY,
                secretAccessKey: process.env.REACT_APP_S3SECRET_KEY,
            }
            const ReactS3Client = new S3(config);
            const fileUrl = process.env.REACT_APP_CDN_URL + "drivers/" + fileName;
            ReactS3Client
                .uploadFile(fileData, fileName)
                .then(data => { this.setState({ licenseUrl: fileUrl }) })
                .catch(err => { this.setState({ error: 'File Not uploaded, Please try again' }); })


            this.setState({ isLoading: false });
        } catch (e) {

        }

    }
    render() {
        const override = `
        display: block;
        margin: 0 auto;
        border-color: red;
        position: absolute;
        z-index: 9;
        margin: 0px auto;
        left: 40%;
        `;
        return (

            <div>
                <Header />
                <div>
                    <ClipLoader css={override} style={{ borderColor: 'red' }} color={this.state.loadingColor} loading={this.state.isLoading} size={150} >  </ClipLoader>
                </div>


                <div>
                    <section id="pricing" className="pricing">

                        <div className="container" data-aos="fade-up" style={{ width: '95%!important' }}>

                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>Add Driver</Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                {this.state.error}
                                            </div>
                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>First Name<span style={{ color: 'red' }}>*</span></Form.Label>
                                                        <Form.Control type="text" placeholder="First Name" value={this.state.firstName} onChange={this.setFirstName} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail">
                                                        <Form.Label>Last Name<span style={{ color: 'red' }}>*</span></Form.Label>
                                                        <Form.Control type="text" placeholder="Last Name" value={this.state.lastName} onChange={this.setLastName} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Mobile No<span style={{ color: 'red' }}>*</span></Form.Label>
                                                        <Form.Control type="number" placeholder="Mobile No" value={this.state.mobileNo} onChange={this.setMobileNo} />
                                                    </Form.Group>
                                                </div>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>Email</Form.Label>
                                                        <Form.Control type="email" placeholder="Email" value={this.state.email} onChange={this.setEmail} />
                                                    </Form.Group>
                                                </div>
                                            </div>

                                            <div className="col-12" style={{ display: "grid", gridTemplateColumns: "repeat(2, 1fr)", gridGap: 10 }}>
                                                <div>
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>License No.</Form.Label>
                                                        <Form.Control type="text" placeholder="licenseNo" value={this.state.licenseNo} onChange={this.setLicenseNo} />
                                                    </Form.Group>
                                                </div>
                                                <div >
                                                    <Form.Group controlId="formBasicEmail" >
                                                        <Form.Label>License Upload<spam style={{ color: 'red' }}>*</spam></Form.Label>
                                                        <Form.Control type="file" onChange={this.setLicenseUrl} placeholder="Select RC" />
                                                    </Form.Group>
                                                    <a href={this.state.licenseUrl} target="_blank"> <img src={this.state.licenseUrl} width={100} /></a>
                                                </div>
                                            </div>
                                            <div className="col-12">

                                                {this.state.driverId < 1 ? <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.addDriver.bind(this)}>
                                                        Add Driver
                                                    </Button>
                                                </Form.Group> : <Form.Group controlId="formBasicEmail" style={{ float: 'right' }}>
                                                    <Button variant="primary" type="button" onClick={this.editDriver.bind(this)}>
                                                        Update Driver
                                                    </Button>
                                                </Form.Group>
                                                }
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>

                            <br />
                            <div className="row">
                                <div className="col-lg-12 col-md-12">

                                    <Card>
                                        <Card.Title className="bgColor" style={{ fontSize: 16, fontWeight: 'bold', borderRadius: "5px 5px 0px 0px", padding: 10, color: 'white' }}>My Drivers </Card.Title>
                                        <Card.Body>
                                            <div style={{ color: 'red' }}>
                                                <Table striped bordered hover responsive>
                                                    <thead>
                                                        <tr>
                                                            <th>Diver Name</th>
                                                            <th>Mobile No. </th>
                                                            <th>License No</th>
                                                            <th>Status</th>
                                                            <th>Action</th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>
                                                        {
                                                            this.state.item.map((object, i) => {
                                                                return <tr key={i + 1}>
                                                                    <td>{object.firstName} {object.lastName}</td>
                                                                    <td>{object.mobileNo}</td>
                                                                    <td>{object.idNumber}</td>
                                                                    <td>{object.status}</td>
                                                                    <td>
                                                                        <Button style={{ float: 'right', marginRight: 5 }} variant="primary" type="button" onClick={this.deleteDriver.bind(this, object._id)}>
                                                                            <i className="fa fa-remove iconStyle" aria-hidden="true"></i>
                                                                        </Button>&nbsp;&nbsp;
                                                                        <Button style={{ float: 'right', marginRight: 5 }} variant="primary" type="button" onClick={this.viewDriver.bind(this, object._id)}>
                                                                            <i className="fa fa-eye iconStyle" aria-hidden="true"></i>
                                                                        </Button>

                                                                    </td>
                                                                </tr>;
                                                            })
                                                        }

                                                    </tbody>
                                                </Table>
                                            </div>

                                        </Card.Body>
                                    </Card>
                                </div>

                            </div>
                        </div>

                    </section>


                </div>
            </div>
        );
    }
}

export default withRouter(AddDriver);